<template>
    <div class="home">
        <!-- 完成顶部的 navBar -->
        <van-nav-bar class="mynavbar" fixed>
            <template slot="title">
                <div class="myword">首页</div>
            </template>
        </van-nav-bar>
        <!-- 频道区域 -->
        <van-tabs v-model="activeChannels">
            <!-- title:用来设置标签 -->
            <van-tab v-for="(item, index) in channelList" :key="index" :title="item.name">
                <!-- isLoading：刷新组件的加载状态 -->
                <!-- refresh：下拉刷新时会触发的事件 -->
                <van-pull-refresh v-model="item.isLoading" @refresh="onRefresh">
                    <!-- list 用来显示数据 -->
                    <!-- v-model：list 的加载状态：是否正在加载数据 -->
                    <!-- finished：list 中数据是否已经全部加载完毕 -->
                    <!-- onLoad：方法 list 用来加载数据的方法 -->
                    <van-list v-model="item.loading" :finished="item.finished" finished-text="没有更多了" @load="onLoad">
                        <!-- 显示一个标题 -->
                        <van-cell class="article" v-for="(subitem, subindex) in item.articleList" :key="subindex">
                            <template slot="title">
                                <!-- 标题内容 -->
                                {{ subitem.title }}
                                <!-- 图片内容 -->
                                <van-grid :border="false" :column-num="3" v-if="subitem.cover.type > 0">
                                    <van-grid-item v-for="(imgitem, imgIndex) in subitem.cover.images" :key="imgIndex">
                                        <van-image lazy-load :src="imgitem" />
                                    </van-grid-item>
                                </van-grid>
                                <!-- 文章信息区域 -->
                                <div class="message">
                                    <div class="left">
                                        <span>{{ subitem.aut_name }}</span>
                                        <span>{{ subitem.comm_count }}评论</span>
                                        <span>{{ subitem.pubdate | datafilter }}</span>
                                    </div>
                                    <div class="right" @click="openmore(subitem.art_id, subitem.aut_id)">
                                        <van-icon name="cross" />
                                    </div>
                                </div>
                            </template>
                        </van-cell>
                    </van-list>
                </van-pull-refresh>
            </van-tab>
            <!-- 添加一个 iconf -->
            <div class="menuicon">
                <van-icon name="like" @click="showPop" />
            </div>
        </van-tabs>
        <!-- 频道操作面板的弹出层 -->
        <!-- <mychannelpop @input="show=$event" :value="show" /> -->
        <!-- <mychannelpop @update:activeChannels="activeChannels=$event" :activeChannels="activeChannels" :channelList="channelList" v-model="show" /> -->
        <!-- 如果一个数据与一个组件存在双向数据绑定的关系，除了可以使用 v-model 之外  -->
        <!-- 还可以使用 sync -->
        <!-- sync: 可以做两件事 -->
        <!-- 1.0 向子组件传入一个数据 -->
        <!-- 2.0 接收子组件中返回的数据，并且赋值给 sync 修改的数据(返回数据的事件名一定要时： update:activeChannels)-->
        <mychannelpop :activeChannels.sync="activeChannels" :channelList="channelList" v-model="show" />

        <!-- 更多面板  artid: 要操作的文章 id  autid：作者 id -->
        <more @dislike="delArticle" :artid="artid" :autid="autid" v-model="moreshow" />
    </div>
</template>

<script>
// 导入频道方法
import { apiGetChannels } from '../../api/channels'
// 导入文的方法
import { apiGetChannelsArticel } from '../../api/article'
// 导入 localstorage 的操作方法
import { getLocal } from '../../utils/mylocal'
// 导入弹出层
import mychannelpop from '../../components/mychannelpop'
// 导入更多面板
import more from '../../components/more'

export default {
    data() {
        return {
            channelList: [], // 频道的数据源
            activeChannels: 0, // 当前选中的频道的下标
            show: false, // 控制频道操作面板的显示和隐藏
            moreshow: false, // 控制更多操作面板的显示和隐藏
            artid: 0, // 要操作的文章的  id
            autid: 0, // 要拉黑的作者 id
        }
    },
    methods: {
        // 当 list 被加载时执行
        async onLoad() {
            // 得到当前选中的频道数据
            let currentChannels = this.channelList[this.activeChannels]
            // 得到当前频道的 id
            let channelId = currentChannels.id;
            // 发送请求到服务器得到当前 id 下面的文章数据
            let res = await apiGetChannelsArticel({
                channelid: channelId,
                timestamp: currentChannels.timestamp ? currentChannels.timestamp : Date.now()
            })
            // 保存数据
            currentChannels.articleList = [...currentChannels.articleList, ...res.data.data.results]
            // 保存 timestamp
            currentChannels.timestamp = res.data.data.pre_timestamp
            // 判断数据是否为空
            if (res.data.data.results.length === 0) {
                currentChannels.finished = true
            }
            // 关闭加载状态
            currentChannels.loading = false
        },
        // 下拉刷新时触发
        onRefresh() {
            // 得到当前频道
            let currentChannels = this.channelList[this.activeChannels]
            // 清空数据,当前频道下的数据源
            currentChannels.loading = false
            currentChannels.finished = false
            currentChannels.articleList = []
            currentChannels.timestamp = null
            // 关闭下拉状态
            currentChannels.isLoading = false
            // 手动重新请求数据
            this.onLoad()
        },
        // 请求频道数据
        async getChannels() {
            // 得到用户信息
            let user = this.$store.state.user
            // 判断用户是否登录：
            if (user && user.token) { // 说明登录过
                // 直接去服务器中取出来频道数据
                let res = await apiGetChannels()
                // 保存频道数据
                this.channelList = res.data.data.channels
            } else { // 说明没有登录：
                // 得到 localstorage 中频道数据
                let localchannels = getLocal('channels')
                // 判断 localstorage 中是否存在频道数据
                if (localchannels) { // localstorage 存储过频道数据
                    // 取出渲染
                    this.channelList = localchannels
                } else { // localstorage 中不存在频道数据
                    // 去服务器中请求默认数据
                    let res = await apiGetChannels()
                    // 保存频道数据
                    this.channelList = res.data.data.channels
                }
            }
            // 频道数据源已经加载完毕：
            // 给这些频道数据添加额外属性：
            this.setOtherPropToChannelList()
        },
        // 给频道数据源中添加额外的属性
        setOtherPropToChannelList() {
            // 添加额外属性
            this.channelList.forEach(item => {
                // item.loading = false
                // item.finished = false
                // item.articleList = []
                // item.isLoading = false
                // item.timestamp = null
                this.$set(item, 'loading', false)
                this.$set(item, 'finished', false)
                this.$set(item, 'articleList', [])
                this.$set(item, 'isLoading', false)
                this.$set(item, 'timestamp', null)
            })
        },
        // 点击菜单按钮时会触发
        showPop() {
            // 显示面板
            this.show = true
        },
        // 打开更多操作面板
        openmore(artid, autid) {
            // console.log(artid)
            // 得到当前点击文件的 id
            this.artid = artid.toString()
            // 得到当前点击文章的作者 id
            this.autid = autid
            this.moreshow = true
        },
        // 删除当前不喜欢的文章
        delArticle(artid) {
            // 得到当前显示频道数据
            let currentChannel = this.channelList[this.activeChannels]
            // 得到当前频道的文章列表
            let currentArticleList = currentChannel.articleList
            // 从文章列表中删除文章
            currentArticleList.forEach((item, index) => {
                // 如果当前数据源中的一条数据的 id 与传入的文章 id 相同，说明这个文章需要被删除
                if (item.art_id === artid) {
                    // 删除
                    currentArticleList.splice(index, 1)
                }
            })
        }
    },
    created() {
        // 请求频道数据
        this.getChannels()
    },
    components: {
        mychannelpop,
        more
    }
}
</script>

<style lang="less" scoped>
.home {
    margin-top: 90px;
    margin-bottom: 50px;
}

.mynavbar {
    background-color: #3296fa;
    .myword {
        color: #fff;
    }
}

// 给 tab 栏区域的标题设置固定定位
// deep: 由于当前样式中添加了属性 scoped ，所以当前样式无法作用到子组件中 deep 可以让当前样式直接作用到子组件中
/deep/ .van-tabs__wrap {
    position: fixed;
    top: 46px;
    left: 0px;
    width: 90%;
    z-index: 999;
}

.menuicon {
    position: fixed;
    top: 46px;
    right: 0px;
    width: 10%;
    height: 44px;
    text-align: center;
    line-height: 44px;
}

.article {
    .message {
        display: flex;
        justify-content: space-between;
        .left {
            span {
                margin-right: 10px;
                color: #ccc;
            }
        }
        .right {
            border: 1px solid #ccc;
            width: 20px;
            height: 15px;
            text-align: center;
            line-height: 15px;
            color: #ccc;
        }
    }
}
</style>
