// 用来封装所有与用户相关的网络请求
// 导入 http
import http from "../utils/http";

// 登录的方法
function apiLogin({ mobile, code }) {
  return http({
    url: "/authorizations",
    method: "POST",
    data: {
      mobile,
      code
    }
  });
}

// 拉黑作者
function apiBlackList(autid) {
  return http({
    url: "/user/blacklists",
    method: "POST",
    data: {
      target: autid
    }
  });
}

// 关注作者
function apiFollowUse(autid) {
  return http({
    url: "/user/followings",
    method: "POST",
    data: {
      target: autid
    }
  });
}

// 取关作者
function apiUnFollowUse(autid) {
  return http({
    url: `/user/followings/${autid}`,
    method: "DELETE"
  });
}

// 获取用户自己的信息
function apiGetUserInfo() {
  return http({
    url: "/user"
  });
}

// 获取个人信息
function apiGetUserProfile() {
  return http({
    url: "/user/profile"
  });
}

// 修改用户个人信息
function apiSetUserProfile({ name, gender, birthday, intro }) {
  return http({
    url: "/user/profile",
    method: "PATCH",
    data: {
      name,
      gender,
      birthday,
      intro
    }
  });
}

// 修改用户的头像
function apiSetUserImg(photo) {
  // axios 默认不能将图片对象传入到服务器
  // 解决方案：使用 formData 来传
  // 创建一个 fd 对象
  var fd = new FormData();
  // 添加参数
  fd.append("photo", photo);
  return http({
    url: "/user/photo",
    method: "PATCH",
    data: fd
  });
}

// 按需导出
export {
  apiLogin,
  apiBlackList,
  apiFollowUse,
  apiUnFollowUse,
  apiGetUserInfo,
  apiGetUserProfile,
  apiSetUserProfile,
  apiSetUserImg
};
