// 处理所有与文章相关的网络请求
// 导入 http
import http from "../utils/http";

// 根据 id 得到频道下面的文章数据
function apiGetChannelsArticel({ channelid, timestamp }) {
  return http({
    // 如果 axios 设置了 baseUrl ，将来只要将请求的地址写全就可以将 baseUrl 覆盖掉
    url: "http://ttapi.research.itcast.cn/app/v1_1/articles",
    params: {
      channel_id: channelid,
      timestamp: timestamp,
      with_top: 0
    }
  });
}

// 将文章标记为不喜欢
function apiDislike(artid) {
  return http({
    url: "/article/dislikes",
    method: "POST",
    data: {
      target: artid
    }
  });
}

// 举报文章
function apiReportArticle({ artid, type }) {
  return http({
    url: "/article/reports",
    method: "POST",
    data: {
      target: artid,
      type: type,
      remark: ""
    }
  });
}

// 获取搜索的文章数据
function apiGetSearchList({ page, perpage, key }) {
  return http({
    url: "/search",
    method: "GET",
    params: {
      page: page,
      per_page: perpage,
      q: key
    }
  });
}

// 获取文章详情
function apiGetDetail(artid) {
  return http({
    url: `/articles/${artid}`
  });
}

// 对文章进行点赞
function apiZan(artid) {
  return http({
    url: "/article/likings",
    method: "POST",
    data: {
      target: artid
    }
  });
}

// 按需导出
export {
  apiGetChannelsArticel,
  apiDislike,
  apiReportArticle,
  apiGetSearchList,
  apiGetDetail,
  apiZan
};
