<template>
    <div>
        <!-- 用户信息区域 -->
        <div class="message">
            <div class="user">
                <div class="img">
                    <img @click="$router.push('/userinfo')" :src="userObj.photo" alt="">
                    <span>{{ userObj.name }}</span>
                </div>
                <div class="time">
                    <div>今日阅读</div>
                    <div>17分钟</div>
                </div>
            </div>
            <div class="shoucang">
                <div class="dongtai">
                    <div>{{ userObj.art_count }}</div>
                    <div>动态</div>
                </div>
                <div class="gz">
                    <div>{{ userObj.follow_count }}</div>
                    <div>关注</div>
                </div>
                <div class="fans">
                    <div>{{ userObj.fans_count }}</div>
                    <div>粉丝</div>
                </div>
            </div>
        </div>
        <!-- 收藏区域 -->
        <div class="noname">
            <van-grid :column-num="3">
                <van-grid-item icon="star-o" text="收藏" />
                <van-grid-item icon="clock-o" text="历史" />
                <van-grid-item icon="records" text="作品" />
            </van-grid>
        </div>
        <!-- 其它区域 -->
        <van-cell-group>
            <van-cell title="消息通知" is-link />
            <van-cell title="用户反馈" is-link />
            <van-cell title="小智同学" @click="$router.push('/zhi')" is-link />
        </van-cell-group>
    </div>
</template>

<script>
// 导入接口
import { apiGetUserInfo } from '../../api/user'

export default {
    data() {
        return {
            userObj: {}
        }
    },
    methods: {
        // 得到用户自己的信息
        async getUserIno() {
            // 请求服务器
            let res = await apiGetUserInfo()
            // 保存用户信息
            this.userObj = res.data.data
        }
    },
    mounted() {
        // 请求用户自己的信息
        this.getUserIno()
    }
}
</script>

<style lang="less" scoped>
.message {
    background-color: #3e9df8;
    padding: 30px 20px;
    .user {
        overflow: hidden;
        .img {
            img {
                width: 50px;
                height: 50px;
                border-radius: 50%;
                float: left;
                margin-left: 40px;
            }
            span {
                float: left;
                margin-left: 20px;
                color: #fff;
            }
        }
        .time {
            position: fixed;
            top: 50px;
            right: 0px;
            background-color: rgba(0, 0, 0, .3);
            border-radius: 20px 0 0 20px;
            font-size: 12px;
            padding: 0 10px;
            color: #fff;
            text-align: center;
        }
    }
    .shoucang {
        margin-top: 30px;
        display: flex;
        text-align: center;
        color: #fff;
        .dongtai {
            flex: 1;
        }
        .gz {
            flex: 1;
        }
        .fans {
            flex: 1;
        }
    }
}
</style>
