<template>
    <div>
        <!-- 搜索区域 -->
        <!-- v-model: 双向绑定的数据源 show-action：控制右侧的取消按钮是否显示 -->
        <!-- search：将来搜索内容时会触发的事件 它有一个参数：这个参数就是搜索框的中输入的内容 -->
        <!-- cancel：点击右侧的取消按钮时会被触发 -->
        <van-search background="#3296fa" v-model="key" placeholder="请输入搜索关键词" show-action @input="thinkFn" @search="onSearch">
            <template slot="action">
                <div class="cancleBtn" @click="onCancel">
                    取消
                </div>
            </template>
        </van-search>
        <!-- 联想区域 -->
        <van-cell-group v-if="thinkList.length > 0">
            <van-cell @click="onSearch(item.item)" v-for="(item, index) in thinkList" :key="index" icon="search">
                <template slot="title">
                    <div v-html="item.colorItem"></div>
                </template>
            </van-cell>
        </van-cell-group>
        <!-- 搜索历史 -->
        <van-cell-group v-else>
            <van-cell title="搜索历史">
                <template slot="right-icon">
                    <van-icon @click="delAll" name="delete" />
                </template>
            </van-cell>
            <van-cell @click="onSearch(item)" v-for="(item, index) in searchList" :key="index" icon="search" :title="item">
                <template slot="right-icon">
                    <van-icon @click.stop="delHis(index)" name="cross" />
                </template>
            </van-cell>
        </van-cell-group>
    </div>
</template>

<script>
// 导入公用的方法
import { apiGetThinkList } from '../../api/common'
// 导入所有操作 local 的方法
import * as mylocal from '../../utils/mylocal'
export default {
    data() {
        return {
            key: '',
            // 联想的结果
            thinkList: [],
            // 定义一个定时器
            timer: null,
            // 搜索的数据源
            searchList: mylocal.getLocal('history') || []
        }
    },
    methods: {
        // 当输入框进行搜索时触发：点击完成按钮时触发（回车）
        onSearch(value) {
            // value：指的就是输入框中的输入的内容
            // 得到搜索的关键字： value
            // 将搜索的关键字保存到 searchList 中
            this.searchList.unshift(value.trim())

            // 去重
            this.searchList = [...new Set(this.searchList)]

            // 将 searchList 保存到本地
            mylocal.setLocal('history', this.searchList)
            // 跳转到搜索结果页面：
            this.$router.push(`/searchResult/${value}`)
        },
        // 点击取消按钮时会被触发
        onCancel() {
            this.key = ""
            this.thinkList = []
        },
        // 联想方法
        thinkFn() {
            // 清除定时器
            clearTimeout(this.timer)
            // 给定时器重新赋值
            this.timer = setTimeout(async () => {
                // 判断关键字是否为空
                if (this.key.trim().length === 0) {
                    // 将联想的列表重置为空
                    this.thinkList = []
                    // 后缀代码不需要执行
                    return
                }
                // 得到输入的内容 ==> this.key
                let res = await apiGetThinkList(this.key)
                this.thinkList = res.data.data.options
                // 改造返回的关键字
                this.thinkList = this.thinkList.map(item => {
                    /// 使用关键字进行分隔,再将数据进行合并,添加一个span标签
                    return {
                        // 带有高亮的结果
                        colorItem: item.split(this.key).join(`<span style="color: red">${this.key}</span>`),
                        // 原本的数据
                        item: item
                    }
                })
            }, 300)
        },
        // 删除历史数据
        delHis(index) {
            // 删除数据
            this.searchList.splice(index, 1)
            // 将数据重新保存到本地
            mylocal.setLocal('history', this.searchList)
        },
        // 清空搜索历史
        delAll() {
            // 添加一个提示框
            this.$dialog.confirm({
                title: '警告',
                message: '是否清空搜索历史'
            }).then(() => {
                // 清空数据
                this.searchList = []
                // 清空 local
                mylocal.removeLocal('history')
            }).catch(() => {
            });
        }
    }
}
</script>

<style lang="less">
.cancleBtn {
    color: #fff;
}

.van-search__action:active {
    background-color: #3296fa;
    .cancleBtn {
        color: #ccc;
    }
}
</style>
