// 用来封装一个处理时间的过滤器
import Vue from "vue";
// 导入 dayjs
import dayjs from "dayjs";
// 导入 插件：
import relativeTime from "dayjs/plugin/relativeTime";
// 导入 语言包
import "dayjs/locale/zh-cn";

// 扩展插件
dayjs.extend(relativeTime);
// 使用语言包
dayjs.locale("zh-cn");
// 封装一个过滤器
Vue.filter("datafilter", value => {
  // value：使用过滤器的字符串
  return dayjs().from(dayjs(value));
});
