<template>
    <div>
        <!-- 弹出层的面板 -->
        <van-popup :value="value" @input="$emit('input', false)" position="bottom" :style="{ height: '70%' }">
            <!-- 当前评论 -->
            <comment :iscomment="false" :commentItem="commentItem" />
            <van-cell>
                <template slot="title">
                    当前评论的回复
                </template>
            </van-cell>
            <!-- 在 list 中得到回复的数据 -->
            <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
                <!-- 在这里渲染评论的回复 -->
                <comment :iscomment="false" :commentItem="item" v-for="(item, index) in replyList" :key="index" />
            </van-list>
            <!-- 添加一个书写评论的组件 -->
            <!-- commid: 当前评论的 id -->
            <!-- artid: 文章的 id -->
            <write @addComment="addComment" :artid="artid" :commid="commentItem.com_id" :isComment="false" />
        </van-popup>
    </div>
</template>

<script>
// 导入 bus
import bus from '../utils/bus'
// 导入评论的组件
import comment from './comment.vue'
// 导入书写评论的组件
import write from './write'
// 导入操作评论的接口
import { apiGetCommentReply } from '../api/pinglun'

export default {
    //               文章的 id
    props: ['value', 'artid'],
    data() {
        return {
            commentItem: {},
            loading: false,
            finished: false,
            offset: null,
            limit: 10,
            // 评论的回复
            replyList: [],
            // 结束的偏移量
            endid: 0
        }
    },
    methods: {
        async onLoad() {
            // 根据当前评论得到回复的列表
            let res = await apiGetCommentReply({
                commid: this.commentItem.com_id.toString(),
                offset: this.offset,
                limit: this.limit
            })
            // 保存数据
            // 回复的数据：追加状态
            this.replyList = [...this.replyList, ...res.data.data.results]
            // 保存 end_id
            this.endid = res.data.data.end_id
            // 保存 last_id
            this.offset = res.data.data.last_id
            // 判断数据是否加载完毕
            if (this.offset === this.endid) {
                this.finished = true
            }
            // 关闭加载状态
            this.loading = false
        },
        // 添加评论
        addComment(replyObj) {
            // 将服务器返回的评论回复数据进行渲染
            this.replyList.unshift(replyObj)
            // 将当前评论的回复数字加1
            this.commentItem.reply_count = this.commentItem.reply_count + 1
        }
    },
    created() {
        // 接收参数
        bus.$on('passitem', value => {
            // 就是当前回复面板中评论数据
            this.commentItem = value
            console.log(this.commentItem)
        })
    },
    components: {
        comment,
        write
    }
}
</script>

