<template>
    <div class="comment">
        <!-- <h3>添加一个评论组件</h3> -->
        <van-cell>
            <template slot="title">
                <div class="mybox">
                    <div class="img">
                        <img :src="commentItem.aut_photo" alt="">
                    </div>
                    <div class="msg">
                        <div>{{ commentItem.aut_name }}</div>
                        <div>{{ commentItem.content }}</div>
                        <div>
                            <span>{{ commentItem.pubdate | datafilter }}</span>
                            <van-button v-if="iscomment" @click="openreply" size="mini" class="myreply" round type="danger">回复 {{ commentItem.reply_count }}</van-button>
                        </div>
                    </div>
                    <div class="follorw">
                        <van-icon name="like" /> {{ commentItem.like_count }}
                    </div>
                </div>
            </template>
        </van-cell>
    </div>
</template>

<script>
// 导入 bus
import bus from '../utils/bus'
export default {
    // 接收父组件中传入的commentItem：评论的数据源
    // iscomment: 决定回复按钮是否隐藏 true 显示 false 隐藏
    props: ['commentItem', 'iscomment'],
    methods: {
        // 打开 replay
        openreply() {
            // 打开 reply 面板
            this.$emit('openreply', true)
            // 向 reply 中传入数据源
            bus.$emit('passitem', this.commentItem)
        }
    }
}
</script>

<style lang="less" scoped>
.mybox {
    display: flex;
    .img {
        img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
        }
    }
    .msg {
        flex: 1;
        span {
            margin: 0;
        }
    }
}

.myreply {
    margin-left: 10px;
}
</style>
