// 导入 store
import store from "../store";
// 导入 router
import router from "../router";

// 创建一个插件对象
const myplugs = {};
// 给对象添加一个方法 ： install
myplugs.install = function(Vue) {
  // 给 Vue 添加一个实例方法：用来进行登录验证
  Vue.prototype.$login = function() {
    // 得到登录信息
    let user = store.state.user;
    // 判断登录
    if (!user.token) {
      // 跳转到登录页面
      router.push("/checklogin");
      return false;
    }
    return true;
  };
};

// 暴露插件对象
export default myplugs;
