// 封装 axios
import axios from "axios";
// 导入 store
import store from "../store";
// 导入 json-bigint
import jsonbigint from "json-bigint";

// 创建一个新的 axios 对象
// axios.create：
//    根据 axios 创建出一个新的实例来
//      意义在于：
//          同时请求两个服务器的接口：
//              黑马头条的服务器： http://ttapi.research.itcast.cn/app/v1_0
//                  instance1
//              黑马页面的服务器： http://mm.research.itcast.cn/app/v1_0
//                  instance2
// 这个实例单独用来请求黑马头条的服务器
let instance = axios.create({
  baseURL: "http://ttapi.research.itcast.cn/app/v1_0",
  // 可以用来得到服务器响应给 axios 的 json格式的字符串
  transformResponse: [
    function(data) {
      try {
        // data 是服务器响应给 axios 的 json 格式的数据源
        // console.log(data);
        // 将数据源进行转换
        return jsonbigint.parse(data);
      } catch (error) {
        // 如果不能转换，直接返回源数据
        return JSON.parse(data);
      }
    }
  ]
});

// 再创建一个 axios 实例，去发送请求到服务器更新 token
let tokenInstance = axios.create({
  baseURL: "http://ttapi.research.itcast.cn/app/v1_0"
});

// 设置请求拦截器
instance.interceptors.request.use(
  function(config) {
    // 判断用户是否登录：
    let token = store.state.user.token;
    // let user = store.state.user;
    // 如果 token 有内容：说明已经登录，否则未登录
    if (token) {
      // 在请求头中添加 token
      config.headers.authorization = `Bearer ${token}`;
    }
    return config;
  },
  function(error) {
    return Promise.reject(error);
  }
);

// 响应拦截器
instance.interceptors.response.use(
  function(response) {
    return response;
  },
  async function(error) {
    console.log("----------------响应拦截器---------------------");
    // 得到服务器的报错信息 401
    // 判断状态码是否为 401
    if (error.response.status === 401) {
      // 说明当前错误是 token 失效
      // 请求服务器的接口：
      //  url：authorizations
      //  method：PUT
      //  参数：headers --> Authorization ---> refresh_token
      // 得到 refresh_token
      let user = store.state.user;
      // 判断 refreseh_token 是否存在
      if (user && user.refresh_token) {
        // 使用一个新的 axios 实例来发送请求去更新 token
        // 更新 token: 发送请求到服务器
        let res = await tokenInstance({
          url: "/authorizations",
          method: "PUT",
          headers: {
            Authorization: `Bearer ${user.refresh_token}`
          }
        });
        // 将 token 保存以 store 中
        user.token = res.data.data.token; // token refresh_token
        // console.log(user)
        // // 重新设置 token
        store.commit("setUser", user);
        // 到了这一步 token 已经更新完毕了：
        // 接下来需要重新发送请求到服务器，请求未完成的数据
        return instance(error.config) // 重新发送请求到服务器，将响应回来的内容交给 instance 的响应拦截器
      }
    }
    console.log("----------------响应拦截器---------------------");
    return Promise.reject(error);
  }
);

// 暴露 axios 实例
export default instance;
