<template>
    <div class="write">
        <van-search v-model="value" placeholder="写评论" show-action shape="round" @search="writeCommit">
            <div slot="action" @click="writeCommit">发表</div>
        </van-search>
    </div>
</template>

<script>
// 导入评论的接口
import { apiAddComment, apiAddCommentReply } from '../api/pinglun'


export default {
    //   文章 id   是否发送评论    评论的id
    props: ['artid', 'isComment', 'commid'],
    data() {
        return {
            value: ''
        }
    },
    methods: {
        async writeCommit() {
            // 发送评论到服务器
            if (this.isComment === true) {
                // 得到输入框中的内容
                // 将数据提交到服务器
                let res = await apiAddComment({
                    artid: this.artid, // 文章的 id
                    content: this.value
                })
                // 接收返回的评论的对象
                let commentObj = res.data.data.new_obj
                // 将返回的评论数据传给 detail 父组件
                this.$emit('addComment', commentObj)
                // 清空输入框
                this.value = ''
            } else { // 发送评论的回复到服务器
                // target：评论的 id
                // console.log(this.commid.toString())
                // content: 评论回复的内容  
                // this.value
                // art_id：评论所属文章的 id
                // console.log(this.artid)
                let res = await apiAddCommentReply({
                    commid: this.commid.toString(),
                    content: this.value,
                    artid: this.artid
                })
                // 保存服务器返回的评论的回复信息
                let replyObj = res.data.data.new_obj
                // 将参数传给 reply 组件
                this.$emit('addComment', replyObj)
            }
        }
    }
}
</script>

<style lang="less">
.write {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    border-top: 1px solid #ccc;
}
</style>
