// 导入了 Vue
import Vue from "vue";
// 导入 vant
import Vant, { Lazyload } from "vant";
// 导入 vant 的样式
import "vant/lib/index.css";
// 导入了 App.vue （根组件）
import App from "./App.vue";
// 导入了 路由
import router from "./router";
// 导入了 仓库
import store from "./store";
// 导入字体图标
import "./styles/myiconfont.css";
// // 导入 dayjs
// import dayjs from "dayjs";
