// 使用过滤器
import "./filters/datefilter.js";
// 导入自己封装的插件
import myplugs from "./utils/myplugs";

// 使用 vant
Vue.use(Vant);
// 使用 Lazyload 指令
Vue.use(Lazyload);
// 使用插件
Vue.use(myplugs); // 调用 myplugs.install 方法

