<template>
    <div>
        <!-- 头部 -->
        <van-nav-bar class="mynavbar" left-arrow @click-left="$router.back()">
            <template slot="title">
                <div class="myword">小智同学</div>
            </template>
        </van-nav-bar>
        <!-- 聊天区域 -->
        <div class="chat">
            <div v-for="(item, index) in chatArr" :key="index" class="item" :class="item.flag === 'robot' ? 'roboto': 'user'">
                <img src="https://dss1.bdstatic.com/6OF1bjeh1BF3odCf/it/u=1325043162,891607995&fm=74&app=80&f=JPEG&size=f121,140?sec=1880279984&t=b9267fdaa2327fc03b3128ba82d601e9" alt="">
                <div class="word">{{ item.msg }}</div>
            </div>
        </div>
        <!-- 底部 -->
        <div class="write">
            <van-search v-model="value" placeholder="写评论" show-action shape="round" @search="writeCommit">
                <div slot="action" @click="writeCommit">发表</div>
            </van-search>
        </div>
    </div>
</template>

<script>
// 导入 socket.io
import io from 'socket.io-client'
export default {
    data() {
        return {
            socket: null,
            value: '',
            // 聊天的列表
            chatArr: [
                { flag: 'robot', msg: '你好啊，见到你很开心' }
            ]
        }
    },
    methods: {
        // 发送信息到服务器
        writeCommit() {
            // 发送信息
            this.socket.emit('message', {
                msg: this.value,
                timestamp: Date.now()
            })
            // 将用户发送的信息保存到 chatArr
            this.chatArr.push({
                flag: 'user',
                msg: this.value
            })
            // 将输入框中的内容清空
            this.value = ''
        }
    },
    created() {
        // 连接服务器
        this.socket = io(`http://ttapi.research.itcast.cn?token=${this.$store.state.user.token}`)
        // 接收信息
        this.socket.on('message', (msg) => {
            // 将服务器响应的信息保存起来
            this.chatArr.push({
                flag: 'robot',
                msg: msg.msg
            })
        })
    }
}
</script>

<style lang="less">
.mynavbar {
    background-color: #3296fa;
    .myword {
        color: #fff;
    }
}

i.van-icon.van-icon-arrow-left.van-nav-bar__arrow {
    color: #fff;
}

.write {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    border-top: 1px solid #ccc;
}

.chat {
    overflow: auto;
    position: fixed;
    width: 100%; // 如果要给一个元素设置一个高度：为一个区域，可以将元素设置一个定位：同时设置两个方法的定位属性：top/bottom left/right
    left: 0;
    top: 46px;
    bottom: 54px;
    padding: 0 10px;
    box-sizing: border-box;
    .item {
        overflow: hidden;
        margin-top: 20px;
        img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
        }
        .word {
            border: 1px solid #ccc;
            padding: 10px;
            background-color: #e0effb;
            border-radius: 5px;
        }
    }
    .roboto {
        img {
            float: left;
            margin-right: 10px;
        }
        .word {
            float: left;
        }
    }
    .user {
        img {
            float: right;
            margin-left: 10px;
        }
        .word {
            float: right;
        }
    }
}
</style>
