<template>
    <div class="userinfo">
        <van-nav-bar class="mynavbar" left-arrow @click-left="$router.back()" right-text="保存" @click-right="save">
            <template slot="title">
                <div class="myword">
                    个人信息
                </div>
            </template>
        </van-nav-bar>
        <van-cell-group>
            <van-cell title="头像" @click="showImgpop" is-link>
                <template slot="default">
                    <img class="myimg" :src="userInfo.photo" alt="">
                </template>
            </van-cell>
            <van-cell title="昵称" @click="openName" is-link :value="userInfo.name" />
            <van-cell title="介绍" is-link :value="userInfo.intro" />
        </van-cell-group>
        <van-cell-group>
            <van-cell title="性别" is-link :value="usermsg.gender === 0 ? '男': '女'" />
            <van-cell title="生日" is-link :value="usermsg.birthday" />
        </van-cell-group>
        <!-- 添加一个 name 的面板 -->
        <van-popup v-model="nameshow" position="bottom" :style="{ height: '7%' }">
            <van-field v-model="userInfo.name" required />
        </van-popup>
        <!-- 添加一个头像选择面板 -->
        <van-popup v-model="imgpopShow">
            <div class="box">
                <van-button class="mybtn" @click="showimgselect" size="large">从相册中选择</van-button>
            </div>
            <input type="file" @change="changeimg" style="display: none" ref="myfile">
            <div>
                <van-button class="mybtn" size="large">拍照</van-button>
            </div>
            <div>
                <van-button class="mybtn" size="large">取消</van-button>
            </div>
        </van-popup>
    </div>
</template>

<script>
// 导入操作用户的接口
import { apiGetUserInfo, apiGetUserProfile, apiSetUserProfile, apiSetUserImg } from '../../api/user'

import Vue from 'vue';
import { ImagePreview } from 'vant';

Vue.use(ImagePreview);

export default {
    data() {
        return {
            userInfo: {},
            usermsg: {},
            // 控制 name 面板的显示与隐藏
            nameshow: false,
            // 控制 头像 面板的显示与隐藏
            imgpopShow: false
        }
    },
    methods: {
        // 将修改后的数据提交到服务器
        async save() {
            try {
                await apiSetUserProfile({
                    name: this.userInfo.name,
                    intro: this.userInfo.intro,
                    gender: this.usermsg.gender,
                    birthday: this.usermsg.birthday
                })
                // 提示用户信息修改成功
                this.$toast.success('修改用户信息成功')
            } catch (error) {
                // 提示失败
                this.$toast.fail('信息有误')
            }
        },
        // 获取头像 & 昵称 & 介绍       & 性别 & 生日
        //            user           /user/profile
        async getUserInfo() {
            // 获取头像 & 昵称 & 介绍
            let res1 = await apiGetUserInfo()
            // 保存 头像 & 昵称 & 介绍
            this.userInfo = res1.data.data
            // 获取 性别 & 生日
            let res2 = await apiGetUserProfile()
            // 保存 性别 & 生日
            this.usermsg = res2.data.data
        },
        // 打开 name 的修改面板
        openName() {
            this.nameshow = true
        },
        showImgpop() {
            this.imgpopShow = true
        },
        // 点击从相册中选择，弹出图片选择器的面板
        showimgselect() {
            // 得到上传框对象
            let myfile = this.$refs.myfile
            // 触发它的 点击事件
            myfile.click()
        },
        // 当选择的图片发生改变时，会触发
        changeimg() {
            // 得到当前选中的图片
            let myimg = this.$refs.myfile.files[0]
            // 如果要预览图片：
            //  要么将图片转为一个在线地址
            // let imgpath = URL.createObjectURL(myimg) // 不建议使用：因为兼容性太差了
            //  要么将图片转为一个 base64 格式的字符串
            // 使用 fileReader 来将图片对象转为 base64 格式的字符串
            // 创建一个 fileReader 对象
            let reader = new FileReader()
            // 调用转换的方法
            reader.readAsDataURL(myimg)
            // 注册事件： load
            reader.addEventListener('load', () => {
                // console.log(reader.result)
                // 预览图片
                ImagePreview({
                    // 预览的图片地址
                    images: [
                        reader.result
                    ],
                    // 设置关闭事件
                    onClose: () => {
                        // 在关闭事件中执行另一个方法
                        this.setProfile()
                    }
                });
            })

        },
        // 处理预览之后的逻辑
        setProfile() {
            // 关闭选择图片的面板
            this.imgpopShow = false
            // 弹出一个确认框
            this.$dialog.confirm({
                title: '提示',
                message: '是否将当前图片设置为头像'
            }).then(async () => {
                // 得到图片信息
                let myimg = this.$refs.myfile.files[0]
                // 将图片信息提交到服务器
                let res = await apiSetUserImg(myimg)
                // 打印返回回来的图片的地址
                // console.log(res.data.data.photo)
                let imgPath = res.data.data.photo
                // 修改当前页面中的图片地址
                this.userInfo.photo = imgPath
            }).catch(() => {
                console.log('取消')
            });
        }
    },
    mounted() {
        // 得到用户信息
        this.getUserInfo()
    }
}
</script>

<style lang="less">
.mynavbar {
    background-color: #3296fa;
    .myword {
        color: #fff;
    }
}

i.van-icon.van-icon-arrow-left.van-nav-bar__arrow {
    color: #fff;
}

span.van-nav-bar__text {
    color: #fff;
}

.van-nav-bar__text:active {
    background-color: #3296fa;
    color: red
}

.userinfo {
    .myimg {
        width: 30px;
        height: 30px;
        border-radius: 50%;
    }
}

.van-popup.van-popup--center {
    width: 80%;
}

.box .mybtn {
    display: block;
}
</style>
