<template>
    <div>
        <!-- 头部导航 -->
        <van-nav-bar class="mynavbar" left-arrow @click-left="onClickLeft">
            <template slot="title">
                <div class="myword">搜索结果</div>
            </template>
        </van-nav-bar>
        <!-- 文章的渲染 -->
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
                <van-cell @click="toDetail(item.art_id)" v-for="(item, index) in searchList" :key="index">
                    <template slot="title">
                        <!-- 标题 -->
                        <h3>{{ item.title }}</h3>
                        <!-- 图片 -->
                        <van-grid v-if="item.cover.type > 0" :border="false" :column-num="3">
                            <van-grid-item v-for="(subitem, subindex) in item.cover.images" :key="subindex">
                                <van-image :src="subitem" />
                            </van-grid-item>
                        </van-grid>
                        <!-- 信息 -->
                        <div class="msg">
                            <span>{{ item.aut_name }}</span>
                            <span>{{ item.comm_count }} 评论</span>
                            <span>{{ item.pubdate | datafilter }}</span>
                        </div>
                        <!-- 用户交互 -->
                        <van-grid :column-num="3">
                            <van-grid-item>
                                <template slot="text">
                                    <div class="mybxo">
                                        <van-icon @click="comment" name="comment-o" /> 评论
                                    </div>
                                </template>
                            </van-grid-item>
                            <van-grid-item>
                                <template slot="text">
                                    <div class="mybxo">
                                        <van-icon @click="zan" name="like-o" />点赞
                                    </div>
                                </template>
                            </van-grid-item>
                            <van-grid-item>
                                <template slot="text">
                                    <div @click="share" class="mybxo">
                                        <van-icon name="share" /> 分享
                                    </div>
                                </template>
                            </van-grid-item>
                        </van-grid>
                    </template>
                </van-cell>
            </van-list>
        </van-pull-refresh>
    </div>
</template>

<script>
// 导入操作文章的接口
import { apiGetSearchList } from '../../api/article'
export default {
    data() {
        return {
            loading: false,
            finished: false,
            isLoading: false,
            // 搜索的关键字
            key: '',
            // 定义一个当前页
            page: 0,
            // 定义一个页容量
            perpage: 10,
            // 搜索的数据源
            searchList: [],
            // 数据的总条数
            totalcount: 0
        }
    },
    methods: {
        // list 加载时触发的事件
        async onLoad() {
            this.page = this.page + 1
            // 根据关键字发送请求到服务器
            let res = await apiGetSearchList({
                page: this.page,
                perpage: this.perpage,
                key: this.key
            })
            this.searchList = [...this.searchList, ...res.data.data.results]
            // 保存 总条数
            this.totalcount = res.data.data.total_count
            // 判断数据是否加载完毕
            if (this.searchList.length === this.totalcount) {
                this.finished = true
            }
            // 将加载状态重置为 false
            this.loading = false
        },
        // 刷新的事件
        onRefresh() {
            // 清空数据源
            this.loading = false
            this.finished = false
            this.page = 0
            this.searchList = []
            this.totalcount = 0
            this.isLoading = false
            // 重新请求数据
            this.onLoad()
        },
        // 点击左侧的箭头触发的事件
        onClickLeft() {
            // 返回上一个路由
            // console.log('返回上一个路由')
            // 在 vue 中一个插件： vue-router 用来控制路由
            // vue-router 中提供了一个对象 $router
            //      在 $router 中有一个方法 back()：可以用来返回上一个路由
            this.$router.back()
        },
        // 评论的方法：
        comment() {
            // 登录的判断
            // 得到用户的登录信息
            let user = this.$store.state.user
            // 判断
            if (!user.token) {
                // 跳转到登录页面
                this.$router.push('/login')
                return
            }
            console.log('评论啦')
        },
        // 点赞的方法
        zan() {
            // 登录判断： 使用 Vue 实例方法来判断
            if (!this.$login()) {
                return
            }
            console.log('点赞')
        },
        // 分享的方法
        share() {
            if (!this.$login()) {
                return
            }
            console.log('分享')
        },
        // 跳转到详情页面
        toDetail(artid) {
            this.$router.push(`/detail/${artid}`)
        }
    },
    created() {
        this.key = this.$route.params.key
    }
}
</script>

<style lang="less">
.mynavbar {
    background-color: #3296fa;
    .myword {
        color: #fff;
    }
}

i.van-icon.van-icon-arrow-left.van-nav-bar__arrow {
    color: #fff;
}

.mybxo {
    display: flex;
    align-items: center;
}

.msg {
    span {
        margin-right: 10px;
    }
}
</style>
