<template>
    <div class="detail">
        <!-- 头部导航 -->
        <van-nav-bar class="mynavbar" left-arrow @click-left="$router.back()">
            <template slot="title">
                <div class="myword">
                    {{ detailObj.title }}
                </div>
            </template>
        </van-nav-bar>
        <!-- 标题区域 -->
        <h1 class="title">
            {{ detailObj.title }}
        </h1>
        <!-- 作者信息 -->
        <van-cell>
            <template slot="title">
                <div class="box">
                    <div class="img">
                        <img :src="detailObj.aut_photo" alt="">
                    </div>
                    <div class="msg">
                        <div>{{ detailObj.aut_name }}</div>
                        <div>{{ detailObj.pubdate | datafilter }}</div>
                    </div>
                    <div class="follorw">
                        <!-- 注意：如果已经关注，按钮的内容为 已关闭，如果没有关注，按钮的内容为 + 关注 -->
                        <van-button @click="follow" v-if="detailObj.is_followed === false" type="info">+ 关注</van-button>
                        <van-button @click="unfollow" v-if="detailObj.is_followed === true" type="info">已关注</van-button>
                    </div>
                </div>
            </template>
        </van-cell>
        <!-- 内容区域 -->
        <van-cell>
            <template slot="title">
                <div v-html="detailObj.content"></div>
            </template>
        </van-cell>
        <!-- 点赞 & 不喜欢 -->
        <div class="btnbox">
            <!-- attitude：-1 无态度  0 不喜欢 1 喜欢 -->
            <van-button @click="zan" class="mybtn" round plain :type="detailObj.attitude === 1 ? 'danger' : 'default'">
                <van-icon name="like-o" />点赞
            </van-button>
            <van-button @click="dislike" class="mybtn" round plain :type="detailObj.attitude === 0 ? 'danger' : 'default'">
                <van-icon name="delete" />不喜欢
            </van-button>
        </div>
        <!-- 评论区域 -->
        <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
            <!-- item 对应的就是数据源 -->
            <comment :iscomment="true" @openreply="openreply" :commentItem="item" v-for="(item, index) in commentList" :key="index" />
        </van-list>
        <!-- 发表评论区域 传入一个文章 id -->
        <write :isComment="true" :artid="artid" @addComment="addComment" />
        <!-- 添加一个回复面板 -->
        <!-- 传入一个文章的 id -->
        <reply :artid="artid" v-model="replyshow" />
    </div>
</template>

<script>
import comment from '../../components/comment'
import write from '../../components/write'
import reply from '../../components/reply'
// 导入文章的请求接口
import { apiGetDetail, apiZan, apiDislike } from '../../api/article'
// 导入操作作者的接口
import { apiFollowUse, apiUnFollowUse } from '../../api/user'
// 导入操作评论的接口
import { apiGetComment } from '../../api/pinglun'

export default {
    data() {
        return {
            artid: 0, // 文章 id
            detailObj: {}, // 文章详情对象 attitude
            loading: false, // list 的加载状态
            finished: false, // list 的数据是否全部加载完成
            offset: null, // 偏移量
            limit: 10,
            commentList: [], // 评论的数据源
            endid: 0, // 结束的偏移量
            replyshow: false
        }
    },
    methods: {
        // 根据 id 得到文章的详情数据
        async getDetail() {
            let res = await apiGetDetail(this.artid)
            this.detailObj = res.data.data
        },
        // 关注用户
        async follow() {
            // 得到要关注的作者的信息
            let autid = this.detailObj.aut_id
            // 发送请求到服务器
            await apiFollowUse(autid)
            // 手动将关注状态设置为 true
            this.detailObj.is_followed = true
        },
        // 取消关注
        async unfollow() {
            try {
                // 得到要关注的作者的信息
                let autid = this.detailObj.aut_id
                await apiUnFollowUse(autid)
            } catch (error) {
                console.log(error)
            }
            // 手动将关注状态设置为 false
            this.detailObj.is_followed = false
        },
        // 点赞
        async zan() {
            // 发送请求到服务器去对文章进行点赞
            let res = await apiZan(this.artid)
            // 手动修改状态
            this.detailObj.attitude = 1
        },
        // 不喜欢
        async dislike() {
            await apiDislike(this.artid)
            this.detailObj.attitude = 0
        },
        // 加载数据源
        async onLoad() {
            // 请求评论数据
            let res = await apiGetComment({
                artid: this.artid,
                offset: this.offset,
                limit: this.limit
            })
            // 保存评论数据：
            this.commentList = [...this.commentList, ...res.data.data.results]
            this.endid = res.data.data.end_id
            // 保存当前页的偏移量
            this.offset = res.data.data.last_id
            // 判断数据是否加载完毕
            if (this.offset === this.endid) {
                this.finished = true
            }
            // 将 List 的加载状态设置为 false
            this.loading = false
        },
        addComment(commentObj) {
            // 接收返回的评论数据
            // console.log(commentObj)
            // 将评论的数据添加到 commentList 中
            this.commentList.unshift(commentObj)
        },
        // 打开回复面板
        openreply(bool) {
            // 修改 replayshow
            this.replyshow = bool
        }
    },
    components: {
        comment,
        write,
        reply
    },
    created() {
        // 得到文章 id
        this.artid = this.$route.params.artid
        // 根据 id 请求文章详情数据
        this.getDetail()
    }
}
</script>

<style lang="less">
.mynavbar {
    background-color: #3296fa;
    .myword {
        color: #fff;
    }
}

i.van-icon.van-icon-arrow-left.van-nav-bar__arrow {
    color: #fff;
}

.detail {
    margin-bottom: 54px;
}

.detail .title {
    text-align: center;
    font-weight: 400;
    margin: 15px;
}

.box {
    display: flex;
    align-items: center;
    .img {
        img {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            margin-right: 10px;
        }
    }
    .msg {
        flex: 1;
    }
}

.btnbox {
    text-align: center;
    .mybtn {
        margin: 20px;
    }
}
</style>
