// 管理所有与评论相关的网络请求
import http from "../utils/http";

// 获取评论数据
function apiGetComment({ artid, offset, limit }) {
  // aioxs 有一个特点：如果传入的参数 为null,默认情况下 aioxs 会将这个参数从数据中移除掉
  return http({
    url: "comments",
    params: {
      type: "a",
      source: artid, // 文章id
      offset: offset,
      limit: limit
    }
  });
}

// 添加文章的评论数据
function apiAddComment({ artid, content }) {
  return http({
    url: "/comments",
    method: "POST",
    data: {
      target: artid,
      content: content
    }
  });
}

// 获取评论的回复
function apiGetCommentReply({ commid, offset, limit }) {
  return http({
    url: "/comments",
    method: "GET",
    params: {
      type: "c",
      source: commid, // 评论的 id
      offset: offset, // 偏移量
      limit: limit
    }
  });
}

// 添加评论的回复
function apiAddCommentReply({ commid, content, artid }) {
  return http({
    url: "/comments",
    method: "POST",
    data: {
      target: commid,
      content: content,
      art_id: artid
    }
  });
}

export { apiGetComment, apiAddComment, apiGetCommentReply, apiAddCommentReply };
