<template>
    <div>
        <!-- 对话框 -->
        <van-dialog :value="value" :showConfirmButton="false">
            <!-- 选项 -->
            <van-cell-group v-if="isReport === false">
                <van-cell @click="$emit('input', false)">
                    <template slot="title">
                        <van-icon name="cross" />
                    </template>
                </van-cell>
                <van-cell icon="send-gift-o" @click="dislike" title="不感兴趣" />
                <van-cell icon="info-o" @click="report" title="反馈垃圾内容" is-link />
                <van-cell icon="delete" @click="blackList" title="拉黑作者" />
            </van-cell-group>
            <!-- 举报的选项 -->
            <van-cell-group v-if="isReport === true">
                <van-cell icon="arrow-left" @click="isReport = false"></van-cell>
                <van-cell @click="reportArticle(item.id)" v-for="(item, index) in reportList" :key="index" :title="item.name" />
            </van-cell-group>
        </van-dialog>
    </div>
</template>

<script>
// 导入文章的操作接口
import { apiDislike, apiReportArticle } from '../api/article'
// 导入用户的操作接口
import { apiBlackList } from '../api/user'

export default {
    // 更多面板的显示&隐藏 要操作的文章id 作者 id
    props: ['value', 'artid', 'autid'],
    data() {
        return {
            // 举报的类型
            reportList: [
                { id: 0, name: '其他问题' },
                { id: 1, name: '标题夸张' },
                { id: 2, name: '低俗色情' },
                { id: 3, name: '错别字多' },
                { id: 4, name: '旧闻重复' },
                { id: 5, name: '广告软文' },
                { id: 6, name: '内容不实' },
                { id: 7, name: '涉嫌违法犯罪' },
                { id: 8, name: '侵权' }
            ],
            // 是否显示举报的类型
            isReport: false
        }
    },
    methods: {
        // 点击不感兴趣时会触发
        async dislike() {
            // 将要删除的文章 id 重新传回给 home
            this.$emit('dislike', this.artid)
            try {
                // 将文章发送到服务器，标记为不喜欢
                await apiDislike(this.artid)
            } catch (error) {
                console.log(error.message)
            }
            // 关闭更多面板
            this.$emit('input', false)
        },
        // 点击反馈按钮时触发
        report() {
            // 打开举报选项的面板
            this.isReport = true
        },
        // 举报文章
        async reportArticle(type) {
            // 发送请求到服务器
            try {
                await apiReportArticle({
                    artid: this.artid,
                    type: type
                })
            } catch (error) {
                console.log(error.message)
            }
            // 关闭面板
            this.isReport = false
            this.$emit('input', false)
            // 提示用户
            this.$toast('您的举报信息我们已经收到，会在3个工作日之内给您回复，请求耐心等候')
        },
        // 拉黑作者
        async blackList() {
            // 得到作者 id
            // this.autid
            try {
                await apiBlackList(this.autid)
            } catch (error) {
                console.log(error.message)
            }
            // 关闭面板
            this.$emit('input', false)
        }
    }
}
</script>

